<template>
    <!-- header -->
    <nv-head :page-type="pageType"
            :show-menu="showMenu" :hd-title="hdTitle">
    </nv-head>

    <section class="page-content">
    	<div class="post-box">
	        <div class="textarea-box">
	            <textarea class="date-desc" id="dateDesc" placeholder="100" autofocus></textarea>
	            <div class="bar">
	                <div class="bar-con">
	                    <div class="opt" id="optWhat"></div>
	                    <div class="opt" id="optWhere"></div>
	                    <div class="opt" id="optWhen"></div>                
	                </div>
	                <a id="sendBtn" class="btn btn-small btn-primary" @click="send()">
	                    <span class="iconfont icon-send"></span>
	                </a>
	            </div>
	        </div>
	        <div class="post-option">
	            <a class="opt-item" :class="currentOpt=='what'?'active':''" href="javascript:;" @click="switchOpt('what')">
	                <span class="iconfont icon-idea"></span>
	            </a>
	            <a class="opt-item" :class="currentOpt=='where'?'active':''" href="javascript:;" style="line-height:44px;" @click="switchOpt('where')">
	                <span class="iconfont icon-location"></span>
	            </a>
	            <a class="opt-item" :class="currentOpt=='when'?'active':''" href="javascript:;" @click="switchOpt('when')">
	                <span class="iconfont icon-calendar"></span>
	            </a>
	        </div>
	        <div class="panels">
	            <div class="opt-panel what-block" :class="currentOpt=='what'?'open':''">
	                <h3></h3>
	                <ul class="what-list">
	                    <li class="what-item" data-val="2">
	                        <span class="iconfont icon-coffee"></span>/
	                    </li>
	                    <li class="what-item" data-val="3">
	                        <span class="iconfont icon-food"></span>
	                    </li>
	                    <li class="what-item" data-val="4">
	                        <span class="iconfont icon-film"></span>
	                    </li>
	                    <li class="what-item" data-val="11">
	                        <span class="iconfont icon-flight"></span>
	                    </li>
	                    <li class="what-item" data-val="5">
	                        <span class="iconfont icon-run"></span>
	                    </li>
	                    <li class="what-item" data-val="7">
	                        <span class="iconfont icon-badminton"></span>
	                    </li>
	                    <li class="what-item" data-val="8">
	                        <span class="iconfont icon-riding"></span>
	                    </li>
	                    <li class="what-item" data-val="9">
	                        <span class="iconfont icon-drive"></span>
	                    </li>
	                    <li class="what-item" data-val="6">
	                        <span class="iconfont icon-photo"></span>
	                    </li>
	                    <!-- <li class="what-item" data-val="10">
	                        <span class="iconfont icon-shopping"></span>
	                    </li> -->
	                    <li class="what-item selected" data-val="1">
	                        <span class="iconfont icon-lquote"></span>
	                    </li>
	                </ul>
	            </div>
	            <div class="opt-panel where-block" :class="currentOpt=='where'?'open':''">
	                <h3></h3>
	                <ul class="where-list">
	                    <li class="where-item">~</li>                
	                </ul>
	            </div>
	            <div class="opt-panel when-block" :class="currentOpt=='when'?'open':''">
	                <h3></h3>
	                <ul class="when-list">
	                    <li class="when-item" data-val="1"></li>
	                    <li class="when-item" data-val="2"></li>
	                    <li class="when-item" data-val="0"></li>
	                    <li class="when-item" data-val="3">
	                        <a href="javascript:;" class="ar-lnk"></a>
	                        <input type="text" id="calLink" value="" />
	                    </li>
	                </ul>
	            </div>
	        </div>
	    </div>
    </section>
</template>
<script>
	var Apimap = require('../libs/apimap');
    var Tips = require('../components/tips');
    var Ajax = require('../components/ajax');
    var Loading = require('../components/loading');
    var Nodata = require('../components/nodata');
    var Dialog = require('../components/dialog');
    var Calendar = require('../components/calendar');

	var now = new Date(),
        today = new Date(now.setHours(0, 0, 0, 0)),
        tommorrow = new Date(today.getTime() + 1 * 86400 * 1000),
        last30days = new Date(today.getTime() + 29 * 86400 * 1000);

    export default {
        ready (){
        	var me = this;

        	window.onload = function() {
	            $('#dateDesc').focus();
	            console.log($('.textarea-box').height());
	    		var listH = $(window).height() - $('.textarea-box').height() - $('.post-option').height() - 50;
	            $('.panels').height(listH);
	    		$('.opt-panel').height(listH);

	    		// 
	            new Calendar({
	                dateFormat: 'yyyy-mm-dd',
	                value: '',
	                multiple: false,
	                minDate: today,
	                closeOnSelect: true,
	                input: $('#calLink'),
	                footer: true,
	                onChange: function (p, values) {
	                	var date = Utils.formatDate(new Date(values[0]));
	                	date = date.replace(/\-/g,'/');
	                	$('#optWhen').html('<span class="iconfont icon-calendar"></span>'+date).show();
	                	me.getOptWhereWidth();
	                }
	            });
            }
        },
        data (){
            return {
                showMenu: false,
                pageType: 'add',
                hdTitle: '',
                currentOpt: 'what',
                submitData: {
                	typeId: 1
                }
            }
        },
        methods:{ 
        	switchOpt : function(opt) {
        		this.currentOpt = opt;
        	},

        	getOptWhereWidth : function() {
            	var conW = $('.bar-con').width(),
            		whatW = $('#optWhat').width(),
            		whenW = $('#optWhen').width();

            	var whereW = conW - whatW - whenW - 33;
            	$('#optWhere').css('maxWidth', whereW);
            },

            send: function () {
            	var desc = $.trim($('#dateDesc').val());

            	if(desc === '') {
            		Tips.show({
	                    type: 'error',
	                    title: ''
	                });
            	} else {
            		this.submitData.article = desc;
            		if($('#calLink').val() !== '') {
            			this.submitData.datingTime = $('#calLink').val();
            		}
            		console.log(this.submitData);
            		this.doSubmit();
            	}
            },

	        // 
	        doSubmit: function () {
	            var me = this;

	            Loading.show();
	           
	            Ajax.post(Apimap.postDatingApi, {
						'datingInfo': JSON.stringify(me.submitData)
					},
					function(d){
						Loading.hide();

					    Tips.show({
		                    type: 'success',
		                    title: ''
		                });
					},
					function(d){
						Loading.hide();

					    Tips.show({
		                    type: 'error',
		                    title: d.resultMsg
		                });
					}
				);

	        }
        },
        components:{
            "nvHead":require('./header.vue')
        }
    }
</script>
<style lang="less">
	@import "../assets/less/common/variables.less";
	@import "../assets/less/common/mixins.less";

    body {
		position: relative;
		height: 100%;
		background: #fff;
		overflow: hidden;
	}
	.post-box {
		.textarea-box {
			position: relative;
			.date-desc {
				display: block;
				padding: 10px 10px 50px;
				width: 100%;
				height: 206px;
				font-size: 16px;
				background: #fff;
				.border-box();
			}
			.bar {
				position: absolute;
				z-index: 9;
				left: 0;
				bottom: 0;
				padding: 0 80px 0 10px;
				width: 100%;
				height: 40px;
				.border-box();
				.bar-con {
					line-height: 30px;
					border-top: 2px dotted @borderColor;
					.clearfix();
				}
				.opt {
					display: none;
					float: left;
					margin-right: 15px;
					color: #999;
					&:last-child {
						margin-right: 0;
					}
					.iconfont {
						margin-right: 5px;
						font-size: 12px;
					}
				}
				#optWhere {
					.text-overflow();
				}
				#sendBtn {
					position: absolute;
					z-index: 10;
					right: 10px;
					top: 0;
				}
			}
		}
		.post-option {
			width: 100%;
			height: 44px;
			.flex();
			.opt-item {
				position: relative;
				line-height: 40px;
	        	text-align: center;
	        	font-size: 20px;
	        	color: #666;
				background: @gray;
				.flex-avg();
				border-top: 1px solid @borderColor;
				border-right: 1px solid @borderColor;
		        &:last-child {
		            border-right: 0;
		        }
		        &.active {
		        	color: #fff;
		        	background: @green-sea;
		        }
			}
		}
		.panels {
			position: relative;
			overflow: hidden;
		}
		.opt-panel {
			position: absolute;
			z-index: 99;
			top: 0;
			left: 0;
			width: 100%;
			opacity: 0;
			.translate3d(0%,100%,0);
			.transition(400ms);
			font-size: 14px;
			background: @light-gray;
			overflow-y: auto;
			&.open {
				opacity: 1;
				.translate3d(0%,0%,0);
			}
			h3 {
				margin: 0;
				padding-left: 15px;
				height: 35px;
				line-height: 35px;
				color: #666;
				font-size: 16px;
				font-weight: 500;
				background: @white;
			}
			.what-list {
				.clearfix;
				color: #666;
				.what-item {
					position: relative;
					float: left;
					padding-left: 15px;
					width: 50%;
					height: 50px;
					line-height: 50px;
		        	text-align: left;
		        	border-bottom: 1px solid @borderColor;
		        	.border-box();
					&:nth-child(odd) {
						border-right: 1px solid @borderColor;
					}
					&.selected {
						color: @green-sea;
					}
					.iconfont {
						margin-right: 10px;
					}
				}
			}
			.where-list {
				color: #666;
				.where-item {
					padding-left: 15px;
					height: 60px;
					line-height: 60px;
		        	border-bottom: 1px solid @borderColor;
				}
			}
			.when-list {
				color: #666;
				.when-item {
					position: relative;
					padding-left: 15px;
					height: 60px;
					line-height: 60px;
		        	border-bottom: 1px solid @borderColor;
					&.selected {
						color: @green-sea;
						.ar-lnk {
							color: @green-sea;
						}
					}
		        	#calLink {
		        		position: absolute;
		        		z-index: 9;
		        		top: 0;
		        		left: 0;
		        		opacity: 0;
		        		width: 100%;
		        		height: 70px;
		        	}
				}
			}
		}
	}

</style>




